:root {
  --height: 94.6vh;
  --main-theme-color-black: #191308;
  --main-theme-color-gray: #322a26;
  --main-theme-color-lightblue: #53d8fb;
  --main-theme-color-darkblue: #66c3ff;
  --main-theme-color-white: #dce1e9;
  --main-theme-color-red: #a50000;
  --main-theme-color-lightred: #c40000;
  --font: "Poppins", sans-serif;
  --font-w-2: 200;
  --font-w-3: 300;
  --font-w-5: 500;
  --font-w-6: 600;
  --font-w-8: 800;
}
.content {
  width: 100%;
  height: var(--height);
}
.content img {
  width: 100%;
  height: var(--height);
}
.content-container {
  position: absolute;
  max-width: 50%;
  min-width: 960px;
  height: var(--height);
  background-color: var(--main-theme-color-black);
  clip-path: polygon(0% 0%, 70% 0%, 85% 50%, 70% 100%, 0% 100%);
  box-shadow: 10px 0px 0px red;
  overflow: hidden;
}
.welcome-message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 70px;
  height: 100%;
  color: var(--main-theme-color-white);
  font-family: var(--font);
}
.welcome-message h1 {
  font-size: 40px;
  font-weight: var(--font-w-5);
}
.welcome-message span {
  font-size: 17px;
  font-weight: var(--font-w-3);
}
.top-highlight {
  position: absolute;
  width: 29px;
  height: 500px;
  left: 744px;
  top: -20px;
  transform: rotate(-17deg);
  background-color: var(--main-theme-color-lightblue);
}
.bottom-highlight {
  position: absolute;
  width: 29px;
  height: 500px;
  left: 737px;
  top: 428px;
  transform: rotate(-163deg);
  background-color: var(--main-theme-color-lightblue);
}

#sign-up {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  right: 30%;
  top: 50%;
  width: 150px;
  height: 45px;
  border: 1px solid #322a2687;
  border-radius: 25px;
  font-family: var(--font);
  font-size: 14px;
  font-weight: var(--font-w-6);
  background-color: var(--main-theme-color-darkblue);
  transition-duration: 0.3s;
}
#sign-up:hover {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  right: 30%;
  top: 50%;
  width: 150px;
  height: 45px;
  border: 1px solid #322a2687;
  border-radius: 25px;
  background-color: var(--main-theme-color-lightblue);
  transition-duration: 0.3s;
}
